import eventbus from '../controller/eventbus.js';
// import gasJSON from '../model/燃气.json';
import gasDatas from '../model/gasDatas.js';

let rootView, graphView, graphViewWrapper, relativeLayout;
export default function (view) {
    rootView = view;
    relativeLayout = view.findViewById('contentRelative', true);
    graphViewWrapper = view.findViewById('contentHTView', true);
    graphView = graphViewWrapper.getContent();

    graphView.setMovableFunc(data => false);
    //graphView.dm().deserialize(gasJSON);
    graphView.deserialize('displays/燃气.json',function(){

    })
    eventbus.on('click_nav', handleClickNav);


    setInterval(function () {
        // 更新数据
        for (var key in gasDatas) {
            gasDatas[key] = Math.round(Math.random() * 100) / 100;
        }

        // update datas
        graphView.dm().each(function (data) {
            var dataBindings = data.getDataBindings();
            if (dataBindings) {
                // update attrs
                for (var name in dataBindings.a) {
                    var db = dataBindings.a[name];
                    var value = gasDatas[db.id];
                    if (db.func) {
                        value = db.func(value);
                    }
                    data.a(name, value);
                }
                // update styles
                for (var name in dataBindings.s) {
                    var db = dataBindings.s[name];
                    var value = gasDatas[db.id];
                    if (db.func) {
                        value = db.func(value);
                    }
                    data.s(name, value);
                }
                // update properties
                for (var name in dataBindings.p) {
                    var db = dataBindings.p[name];
                    var value = gasDatas[db.id];
                    if (db.func) {
                        value = db.func(value);
                    }
                    data[ht.Default.setter(name)](value);
                }
            }
        });
    }, 3000);
}

